<template>
  <button class="qui-btn" @click="btnClickEventSub">
    <slot name="icon"></slot>
    <span>{{msg}}</span>
  </button>
</template>

<script>
  export default {
    props: {
      msg: {
        default: '下载'
      }
    },
    methods: {
      btnClickEventSub: function () {
        this.$emit('btnClickEvent')
        console.log('-------btnClickEvent--------')
      }
    }
  }
</script>
<style scoped>
  @import './css/reset.import.css';
  @import './css/qui-btn.import.css';
</style>




